import React, {memo, useCallback, useEffect, useRef} from 'react';
import {goBack, styled, useAutoToastError, useBoolean} from '@true/core';
import {CustomHeader} from '@/components/CustomHeader';
import {uploadImage} from '@/utils/helper';
import {
  IEditCompany,
  IEditPermissionChannel,
  initEditCompany,
  initEditPermissionChannel,
  requestEditCompanyInfo,
  requestEditPermissionChannel,
} from '@/store/settings/function';
import {useSetting} from '@/store/settings';
import {DiscardDialog} from '@/components/DiscardDialog';
import {
  AnimatedKeyboardAvoidingViewRef,
  FormScroll,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  UIInput,
} from '@true/ui';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {ImageDTO} from '@/components/CameraRollPicker/useGallery';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import Animated, {
  runOnUI,
  useAnimatedRef,
  useSharedValue,
} from 'react-native-reanimated';
import {ETypeChannel} from '@/store/channels/type';
import {EPermissionChannels} from '@/store/settings/type';
import LoadingModal from '@/components/LoadingModal';

const PermissionSetting = memo(function PermissionSetting() {
  const setting = useSetting();
  const config = setting?.sysinfo.config;
  const [visibleDialog, showDialog, hideDialog] = useBoolean();
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useAnimatedRef<any>();
  const params = useSharedValue<IEditPermissionChannel>(
    initEditPermissionChannel,
  );

  useEffect(() => {
    let _params = {
      ...initEditPermissionChannel,
    };
    if (config) {
      _params = {
        create_channel: config.create_channel,
        create_company_channel: config.create_company_channel,
        create_channel_extra: config.create_channel_extra,
        create_company_channel_extra: config.create_company_channel_extra,
      };
    }
    params.value = _params;
  }, [config, params]);

  const typeChannelOptions = [
    {
      label: 'Everyone',
      value: EPermissionChannels.EVERYONE,
    },
    {
      label: 'App admins and owners',
      value: EPermissionChannels.ADMIN,
    },
    {
      label: 'Owners only',
      value: EPermissionChannels.OWNER,
    },
  ];

  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      await requestEditPermissionChannel(params.value);
      goBack();
    }, []);
  useAutoToastError(createError);

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  return (
    <ScreenWrapper>
      <MyKeyboardAvoidingView ref={ref} scrollRef={scrollRef}>
        <CustomHeader
          title={'Channels permissions'}
          isSearch={false}
          isBack
          onCreate={onPressSubmit}
          okText={'Save'}
        />
        <LoadingModal isVisible={loading} />
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          <SContainer>
            <UIInput.Selector
              title={'Permission to create regular channels'}
              keyName={'create_channel'}
              params={params}
              options={typeChannelOptions}
              placeholder={'Everyone'}
            />
            <STitle>Company channels</STitle>
            <UIInput.Selector
              title={'Permission to create company channels'}
              keyName={'create_company_channel'}
              params={params}
              options={typeChannelOptions}
              placeholder={'Everyone'}
            />
            {/*<SWrapperInput>*/}
            {/*  <MyCheckBox*/}
            {/*    defaultValue={watch('mv_sticky')}*/}
            {/*    control={control}*/}
            {/*    keyName={'mv_sticky'}*/}
            {/*    title={'Make missions and visions highlighted on the homepage'}*/}
            {/*    value={watch('mv_sticky')}*/}
            {/*  />*/}
            {/*</SWrapperInput>*/}
            <AnimatedBottomSpace />
          </SContainer>
        </SScrollView>
      </MyKeyboardAvoidingView>
      <DiscardDialog
        title={'Discard change'}
        content={
          'You have made changes to this item that have not been saved. If you leave this page, your changes will be lost.'
        }
        visible={visibleDialog}
        onClose={hideDialog}
      />
    </ScreenWrapper>
  );
});

export default PermissionSetting;

const STitle = styled.Text`
  color: #22313f;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
`;
const SContainer = styled(Animated.View)`
  flex: 1;
  gap: 12px;
  padding: 12px 16px;
`;

const SScrollView = styled.ScrollView`
  flex: 1;
  background-color: white;
  margin-top: 8px;
`;
